<template>
  <div class="card">
    <div class="card-header border-0">
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0">Tables</h3>
        </div>
        <div class="col text-right">
          <base-button size="sm" type="primary" @click="toggleTableModal()">Add Table</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table thead-classes="thead-light" :data="tableData">
        <template slot="columns">
          <th>Name</th>
          <th>Fields</th>
          <th>Action</th>
        </template>

        <template slot-scope="{row}">
          <th scope="row">{{row.name}}</th>
          <td>{{row.fields.length}}</td>
          <td>
            <base-button size="sm" type="primary" iconOnly icon="ni ni-ruler-pencil" title="Edit"></base-button>
          </td>
        </template>
      </base-table>
    </div>
    <modal :show="showModal">testing the modal slot</modal>
  </div>
</template>
<script>
export default {
  name: "data-tables-table",
  data() {
    return {
      tableData: [],
      showModal: true
    };
  },
  mounted() {
    var dt = this;
    this.$http.get("/api/data-tables").then(response => {
      for (var d in response.data) {
        dt.tableData.push({
          id: response.data[d].id,
          name: response.data[d].name,
          fields: response.data[d].data_fields
        });
      }
    });
  },
  methods() {
    return {
      toggleTableModal: function() {
        console.log("toggling");
        this.showModal = !this.showModal;
      }
    };
  }
};
</script>
